@import url(lib/reset.css);
.nav-item + .nav-item {
  margin-bottom: 20px; }

.nav-sub {
  padding-left: 30px; }

.discussion-wrap {
  height: 500px;
  margin-top: 20px;
  background: #f9f9f9;
  overflow: auto; }

.discussion {
  list-style: none;
  margin: 0; }
  .discussion .message {
    background: #f9f9f9;
    padding: 10px;
    border-radius: 2px; }
    .discussion .message.self {
      text-align: right; }
    .discussion .message .message-name {
      padding-left: 2px;
      margin-bottom: 5px; }
      .discussion .message .message-name h6 {
        font-weight: 400 !important;
        display: inline-block;
        margin: 0 !important; }
    .discussion .message .ui.card {
      width: auto;
      background: #fff;
      max-width: 230px;
      margin: 0 !important;
      display: inline-block !important;
      border: none !important; }
      .discussion .message .ui.card .content {
        padding: 0.7em 1em; }
    .discussion .message p {
      font-size: 0.8em;
      margin: 0 0 0.2em 0; }
    .discussion .message time {
      bottom: 5px;
      display: inline-block;
      position: relative;
      font-size: 0.7em;
      color: #ccc;
      margin: 0 10px; }
    .discussion .message .cnt {
      bottom: 5px;
      display: inline-block;
      position: relative;
      font-size: 0.7em;
      color: #ffc211;
      margin: 0 5px; }
  .discussion.self {
    -ms-flex-pack: end;
    justify-content: flex-end;
    -ms-flex-align: end;
    align-items: flex-end; }



